/*
 * Global Styles
 */
body {
  background-color: #131a20;
  background-image: url(../img1/bg3.jpg);
  background-position: top center;
  background-repeat: no-repeat;
  background-size:contain;
  color: #84878d;
  font-family: "Roboto", "Open Sans", sans-serif;
  font-size: 15px;
  font-weight: 400;
  line-height: normal;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 700;
  margin: 0 0 20px;
}

hr {
  border: none;
  border-bottom: 1px solid #777;
}

ul,
ol {
  margin: 0;
  padding-left: 0;
}

a {
  text-decoration: none;
  color: #ffaa3c;
}

