  font-style: normal;
}

p {
  margin-top: 0;
}

form input,
form textarea,
form select {
  outline: none;
  border: 1px solid #ccc;
  padding: 10px;
  border-radius: 5px;
  outline: none;
}

form select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

form textarea {
  resize: vertical;
}

/*
 * Reusable Components Style
 */
.button,
form input[type="submit"],
form button,
form input[type="reset"],
.contact-form input[type="submit"] {
  border: none;
  background: #ffaa3c;
  padding: 10px;
  border-radius: 3px;
  color: #92601e;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.3);
  -webkit-transition: .3s ease;
  transition: .3s ease;
}

.button:hover,
form input[type="submit"]:hover,
form button:hover,
form input[type="reset"]:hover,
.contact-form input[type="submit"]:hover,
.button:focus,
form input[type="submit"]:focus,
form button:focus,
form input[type="reset"]:focus,
.contact-form input[type="submit"]:focus {
  background-color: #ffc06f;
}

.slider {
  position: relative;
  margin-bottom: 30px;
}

.slider .slides {
  list-style: none;
  *zoom: 1;
}

.slider .slides:after {
  content: " ";
  clear: both;
  display: block;
  overflow: hidden;
  height: 0;
}

.slider li {
  border-radius: 5px;
  overflow: hidden;
}

.slider img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.slider .flex-direction-nav {
  list-style: none;
  z-index: 2;
  position: absolute;
  top: 45%;
  width: 100%;
}

.slider .flex-direction-nav a {
  width: 45px;
  height: 45px;
  border-radius: 50%;
  background: white;
  text-align: center;
  line-height: 2.5;
  font-size: 18px;
  font-size: 1.2em;
  display: block;
  position: absolute;
  -webkit-transition: .3s ease;
  transition: .3s ease;
}

.slider .flex-direction-nav a:hover {
  background-color: #ffaa3c;
  color: white;
}

.slider .flex-direction-nav .flex-prev {
  left: 30px;
}

.slider .flex-direction-nav .flex-next {
  right: 30px;
}

.latest-movie {
  margin-bottom: 30px;
  border-radius: 5px;
  overflow: hidden;
}

.latest-movie img {
  max-width: 100%;
  width: 100%;
  display: block;
  height: 100%;
}

.team {
  text-align: center;
  margin: 0 0 30px;
}

.team .team-image {
  width: 230px;
  height: 230px;
  border-radius: 50%;
  overflow: hidden;
  margin: 0 auto 30px;
}

.team .team-image img {
  display: block;
  max-width: 100%;
  width: 100%;
}

.team .team-name {
  font-size: 16px;
  font-size: 1.0666666667em;
  margin: 0;
}

.team .team-title {
  font-size: 12px;
  font-size: 0.8em;
}

.social-links {
  margin: 20px 0;
}

.social-links a {
  color: white;
  display: inline-block;
  width: 30px;
  height: 30px;
  text-align: center;
  border-radius: 50%;
  line-height: 1.9;
  -webkit-transition: .3s ease;
  transition: .3s ease;
}

.social-links .facebook {
  background-color: #204385;
}

.social-links .facebook:hover {
  background-color: #2a58ae;
}

.social-links .twitter {
  background-color: #2aa9e0;
}

.social-links .twitter:hover {
  background-color: #57bbe6;
}

.social-links .google-plus {
  background-color: #d3492c;
}

.social-links .google-plus:hover {
  background-color: #dc6d56;
}

.social-links .pinterest {
  background-color: #ca2128;
}

.social-links .pinterest:hover {
  background-color: #e03e45;
}

.breadcrumbs {
  padding-bottom: 20px;
  margin-bottom: 30px;
  border-bottom: 1px solid #ebebeb;
}

.breadcrumbs a {
  font-weight: 400;
  color: #9ea1a6;
}

.breadcrumbs a:after {
  content: " ";
  width: 8px;
  height: 5px;
  display: inline-block;
  margin-left: 10px;
  margin-right: 10px;
  background: url(../images/arrow-gray.png) no-repeat;
}

.breadcrumbs a:hover {
  color: #ffaa3c;
}

.breadcrumbs span {
  color: #84878d;
}

ul.arrow {
  list-style: none;
}

ul.arrow li {
  padding-left: 20px;
  background: url(../images/arrow.png) no-repeat 0 center;
  margin-bottom: 5px;
}

ul.arrow li a {
  color: #84878d;
  -webkit-transition: .3s ease;
  transition: .3s ease;
}

ul.arrow li a:hover {
  color: #ffaa3c;
}

/* Star rating style */
.star-rating {
  display: inline-block;
  overflow: hidden;
  position: relative;
  top: 2px;
  height: 1em;
  line-height: 1.1;
  font-size: 15px;
  font-size: 1em;
  width: 4.7em;
  font-family: "FontAwesome";
}

.star-rating:before {
  content: "\f005\f005\f005\f005\f005";
  float: left;
  top: 0;
  left: 0;
  position: absolute;
}

.star-rating span {
  overflow: hidden;
  float: left;
  top: 0;
  left: 0;
  position: absolute;
  padding-top: 1.5em;
}

.star-rating span:before {
  content: "\f005\f005\f005\f005\f005";
  top: 0;
  position: absolute;
  left: 0;
  color: #ffaa3c;
}

