/*
 * Header Styles
 */
.site-header {
  padding: 50px 0;
  *zoom: 1;
}

.site-header:after {
  content: " ";
  clear: both;
  display: block;
  overflow: hidden;
  height: 0;
}

#branding {
  float: left;
}

#branding .logo,
#branding .logo-copy {
  display: inline-block;
}

#branding .logo {
  margin-right: 10px;
}

#branding .site-title {
  margin: 0;
  text-transform: uppercase;
  color: white;
  font-size: 16px;
  font-size: 1.0666666667em;
}

#branding .site-title a {
  color: white;
}

#branding .site-description {
  font-size: 13px;
  font-size: 0.8666666667em;
  color: #9198af;
}

.main-navigation {
  float: right;
  background: white;
  border-radius: 5px;
}

.main-navigation .menu-toggle,
.main-navigation .menu,
.main-navigation .search-form {
  display: inline-block;
  vertical-align: middle;
}

.main-navigation .menu-toggle {
  color: #333;
  border: none;
  background: none;
  font-size: 18px;
  font-size: 1.2em;
  padding: 8px 15px;
  display: none;
  outline: none;
}

@media screen and (max-width: 990px) {
  .main-navigation .menu-toggle {
    display: inline-block;
  }
}

@media screen and (min-width: 991px) {
  .main-navigation .menu-toggle {
    display: none !important;
  }
}

.main-navigation .menu {
  list-style: none;
  margin: 0;
}

@media screen and (min-width: 991px) {
  .main-navigation .menu {
    display: inline-block !important;
  }
}

@media screen and (max-width: 990px) {
  .main-navigation .menu {
    display: none;
  }
}

.main-navigation .menu-item {
  display: inline-block;
  padding: 10px 0;
}

.main-navigation .menu-item a {
  padding: 0 15px;
  display: inline-block;
  color: #84878d;
  font-size: 14px;
  font-size: 0.9333333333em;
  font-weight: 400;
  line-height: 1;
  border-right: 1px solid #eeeeef;
}

.main-navigation .menu-item.current-menu-item,
.main-navigation .menu-item:hover {
  position: relative;
}

.main-navigation .menu-item.current-menu-item:after,
.main-navigation .menu-item:hover:after {
  content: " ";
  position: absolute;
  width: 80%;
  height: 3px;
  background-color: #ffaa3c;
  bottom: -3px;
  left: 0;
  right: 0;
  margin: 0 auto;
  display: block;
}

.main-navigation .search-form input {
  background-color: #f0f0f0;
  border: none;
  border-left: 1px solid #eeeeef;
  border-radius: 0;
  font-family: inherit;
  font-weight: 300;
  width: 0;
  visibility: hidden;
  opacity: 0;
  -webkit-transition: .3s ease;
  transition: .3s ease;
  padding: 10px 0;
}

.main-navigation .search-form input.active {
  width: 200px;
  visibility: visible;
  opacity: 1;
  padding: 10px;
}

.main-navigation .search-form button {
  background-color: transparent;
  border: none;
  border-left: 1px solid #eeeeef;
  border-radius: 0;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  margin-left: -5px;
  color: inherit;
  box-shadow: none;
  outline: none;
  -webkit-transition: .3s ease;
  transition: .3s ease;
}

.main-navigation .search-form button.active {
  background-color: #f0f0f0;
}

.mobile-navigation {
  clear: both;
  padding-top: 20px;
  display: none;
}

@media screen and (min-width: 991px) {
  .mobile-navigation {
    display: none !important;
  }
}

.mobile-navigation .menu {
  padding: 10px 20px;
  background-color: white;
  border-radius: 3px;
  list-style: none;
}

.mobile-navigation .menu li {
  border-bottom: 1px solid #eeeeef;
}

.mobile-navigation .menu li:last-child {
  border-bottom: none;
}

.mobile-navigation .menu a {
  color: #84878d;
  padding: 10px;
  display: block;
}

.mobile-navigation .menu a:hover {
  color: #ffaa3c;
}

.main-content .container {
  background: white;
  border-radius: 5px;
  padding: 30px;
}

.section-title {
  font-size: 30px;
  font-size: 2em;
  font-weight: 300;
}

.movie-schedule {
  list-style: none;
}

.movie-schedule li {
  padding: 10px 0;
  border-bottom: 1px solid #d7d8da;
}

.movie-schedule li:last-child {
  border-bottom: none;
}

.movie-schedule .date,
.movie-schedule .entry-title {
  display: inline-block;
}

.movie-schedule .date {
  width: 80px;
  padding: 5px 10px;
  text-align: center;
  font-size: 12px;
  font-size: 0.8em;
  border: 1px solid #d7d8da;
  border-radius: 3px;
  margin-right: 10px;
}

.movie-schedule .entry-title {
  font-size: 15px;
  font-size: 1em;
  font-weight: 400;
  margin: 0;
}

.movie-schedule .entry-title a {
  -webkit-transition: .3s ease;
  transition: .3s ease;
}

.movie-schedule .entry-title a:hover {
  color: #84878d;
}

.no-bullet {
  list-style: none;
}

.leading {
  font-size: 26px;
  font-size: 1.7333333333em;
  font-weight: 300;
}

.page figure {
  margin-bottom: 30px;
}

.page img {
  max-width: 100%;
}

.page .movie-title {
  font-size: 36px;
  font-size: 2.4em;
  font-weight: 300;
}

.filters {
  margin-bottom: 50px;
}

.filters select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  padding: 10px 50px 10px 10px;
  background-color: #e7e7e7;
  border-radius: 5px;
  border: none;
  margin-right: 10px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.3);
  outline: none;
  background-image: url(../images/select.png);
  background-repeat: no-repeat;
  background-position: 100% center;
}

.movie-list {
  margin: 0 -15px;
}

.movie-list .movie {
  width: 25%;
  padding: 0 15px;
  float: left;
  margin-bottom: 30px;
}

@media screen and (min-width: 991px) {
  .movie-list .movie:nth-child(4n+1) {
    clear: both;
  }
}

@media screen and (max-width: 990px) {
  .movie-list .movie {
    width: 50%;
  }

  .movie-list .movie:nth-child(4n+1) {
    clear: none;
  }

  .movie-list .movie:nth-child(2n+1) {
    clear: both;
  }
}

@media screen and (max-width: 480px) {
  .movie-list .movie {
    width: 100%;
    clear: both;
  }
}

.movie-list .movie .movie-poster {
  border-radius: 5px;
  overflow: hidden;
  margin-bottom: 20px;
  padding-top: 30px;
  border: 1px solid transparent;
}

.movie-list .movie .movie-poster img {
  display: block;
  width: 100%;
  max-width: 100%;
  height: auto;
  -webkit-transition: .3s ease;
  transition: .3s ease;
}

.movie-list .movie:hover img {
  -webkit-transform: scale(1.2);
  -ms-transform: scale(1.2);
  transform: scale(1.2);
}

.movie-list .movie .movie-title {
  font-size: 24px;
  font-size: 1.6em;
  font-weight: 300;
  margin-bottom: 20px;
}

.movie-list .movie .movie-title a {
  color: black;
  -webkit-transition: .3s ease;
  transition: .3s ease;
}

.movie-list .movie .movie-title a:hover {
  color: #ffaa3c;
}

.pagination {
  clear: both;
  text-align: center;
}

.pagination .page-number {
  display: inline-block;
  width: 40px;
  height: 40px;
  border-radius: 5px;
  background: #e7e7e7;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
  line-height: 2.6;
  color: #84878d;
  font-size: 15px;
  font-size: 1em;
  -webkit-transition: .3s ease;
  transition: .3s ease;
}

.pagination .page-number.current,
.pagination .page-number:hover {
  background: #ffaa3c;
  color: white;
}

.movie-meta,
.starring {
  margin-bottom: 30px;
